.cardGood
    margin-top: 65px
    &__inner
        display: flex
        padding-bottom: 60px
        &-image
            width: 50%
            height: 475px
        >div
            padding-left: 40px
            >span
                display: block
                padding-top: 35px
                font-weight: 500
                font-size: 14px
                line-height: 130%
                color: #1FD85D
                margin-bottom: 10px
            >h2
                font-weight: 500
                font-size: 30px
                line-height: 150%
                color: #111111
                margin-bottom: 10px
                max-width: 628px
                >span
                    font-weight: 800
                    margin-right: 10px
            >h5
                position: relative
                font-weight: 400
                font-size: 12px
                line-height: 130%
                color: #3F4E65
                margin-bottom: 25px
                padding-left: 25px
                img
                    position: absolute
                    left: 6px
                    top: -1px
            >div
                display: flex
                align-items: center
                h3
                    font-weight: 800
                    font-size: 30px
                    line-height: 130%
                    color: #111111
                    margin-right: 40px
                >div
                    display: flex
                    align-items: center
                    margin-right: 38px
                    button
                        text-align: center
                        width: 34px
                        height: 25px
                        border: none
                        font-weight: 300
                        font-size: 12px
                        font-weight: 700
                        line-height: 15px
                        text-align: center
                        color: #3F4E65
                        background: linear-gradient(90deg, rgba(255, 198, 80, 0.3) 0%, rgba(254, 202, 110, 0.3) 97.25%)
                        border-radius: 1000px
                    h4
                        font-weight: 500
                        font-size: 14px
                        line-height: 130%
                        color: #3F4E65
                        margin: 0 20px
                >button
                    position: relative
                    font-weight: 700
                    font-size: 14px
                    line-height: 17px
                    color: #FFFFFF
                    flex: none
                    order: 0
                    flex-grow: 0
                    padding: 21px 74.5px 21px 39.5px
                    background: #FFC85E
                    border-radius: 79px
                    border: none
                    cursor: pointer
                    img
                        position: absolute
                        top: 19px
                        right: 37px
        &-container-2
            display: flex
            align-items: center
            margin-top: 20px
            &-btn-1
                position: relative
                padding: 0 !important
                height: 77px
                width: 77px
                border: none
                background: #FFFFFF !important
                box-shadow: 0px 15px 70px -11px rgba(43, 28, 1, 0.1)
                border-radius: 10px !important
                margin-right: 14px
                img
                    position: absolute
                    left: 27px
                    top: 26.5px !important
            p
                font-weight: 400
                font-size: 12px
                line-height: 150%
                text-align: center
                color: #3F4E65
                width: 316px
                padding: 20.5px 35.5px
                background: #FFFFFF !important
                box-shadow: 0px 15px 70px -11px rgba(43, 28, 1, 0.1)
                border-radius: 10px !important
                margin-right: 10px
                span
                    font-weight: 800
            &-btn-2
                position: absolute
                height: 77px
                font-weight: 700
                font-size: 14px
                line-height: 17px
                color: #3f4e65 !important
                background: #FFFFFF !important
                box-shadow: 0px 15px 70px -11px rgba(43, 28, 1, 0.1)
                border-radius: 10px !important
                img
                    position: absolute
                    right: 47px !important
                    top: 31.5px !important
        &-info,&-characteristics
            margin-top: 35px
            li
                font-weight: 300
                font-size: 14px
                line-height: 150%
                color: #3F4E65
                span
                    margin-left: 5px
                    font-weight: 500
                    font-size: 14px
                    line-height: 150%
                    color: #111111
        &-characteristics
            h3
                width: max-content
                position: relative
                font-weight: 500
                font-size: 16px
                line-height: 150%
                color: #111111
                margin-bottom: 15px
                cursor: pointer
                &::after
                    position: absolute
                    content: url('../../images/polygon.png')
                    top: -2px
                    right: -12px

        &-description
            margin-top: 30px
            font-weight: 300
            font-size: 12px
            line-height: 150%
            color: #3F4E65
            max-width: 611px
            h3
                width: max-content
                position: relative
                font-weight: 500
                font-size: 16px
                line-height: 150%
                color: #111111
                margin-bottom: 15px
                cursor: pointer
                &::after
                    position: absolute
                    content: url('../../images/polygon.png')
                    top: -2px
                    right: -12px

@media ( max-width: 1410px  )
